	module.exports = {"data":{"markdownRemark":{"html":"<h2>Array.prototype.includes</h2>\n<p>The <code>includes()</code> method determines whether an array includes a value. It returns true or false.  </p>\n<p>It takes two arguments:</p>\n<ol>\n<li><code>searchValue</code> - The element to search for in the array.</li>\n<li><code>fromIndex</code> - The position in the array to start searching for the proivded <code>searchValue</code>. If a negative value is supplied it starts from the array’s length minus the negative value.</li>\n</ol>\n<h3>Example</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code><span class=\"token keyword\">const</span> a <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token number\">1</span><span class=\"token punctuation\">,</span> <span class=\"token number\">2</span><span class=\"token punctuation\">,</span> <span class=\"token number\">3</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\na<span class=\"token punctuation\">.</span><span class=\"token function\">includes</span><span class=\"token punctuation\">(</span><span class=\"token number\">2</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span> <span class=\"token comment\" spellcheck=\"true\">// true </span>\na<span class=\"token punctuation\">.</span><span class=\"token function\">includes</span><span class=\"token punctuation\">(</span><span class=\"token number\">4</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span> <span class=\"token comment\" spellcheck=\"true\">// false</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/includes\">MDN</a></p>","fields":{"slug":"/javascript/standard-objects/array/array-prototype-includes/"},"frontmatter":{"title":"Array.prototype.includes"}}},"pathContext":{"slug":"/javascript/standard-objects/array/array-prototype-includes/"}}

